import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useState } from 'react';
import 'react-time-picker/dist/TimePicker.css';
import { TimePicker } from '@mui/x-date-pickers/TimePicker';
import TextField from '@mui/material/TextField';
import 'assets/css/batteriesAddOrUpdate.css';
import 'assets/css/operatingInfomationAddOrUpdate.css';

function AddOrUpdate() {
  const [startDate, setStartDate] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content batteries__add__update oi__add__update">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">운영정보 관리 → {param.flag}</dt>
          </dl>
        </div>
        <div className="right btn__wrap">
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">삭제</span>
          </button>
          <button className="custom__btn">
            <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>
            <span className="f__medium">저장</span>
          </button>
        </div>
      </div>
      {/* // sub_nav */}
      {/* 기본 */}
      <section>
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">기본</p>
          </div>
          <ul className="default">
            <li>
              <dl>
                <dt>이름</dt>
                <dd>
                  <input className="custom__input" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>설명</dt>
                <dd>
                  <input className="custom__input" />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // 기본 */}
      {/* 배터리 대여 온도 */}
      <section className="device__name grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">배터리 대여 온도</p>
          </div>
          <ul className="battery__rental">
            <li>
              <dl>
                <dt>최저</dt>
                <dd>
                  <input className="custom__input" type="number" />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>최고</dt>
                <dd>
                  <input className="custom__input" type="number" />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // 배터리 대여 온도 */}
      {/* 배터리 대여 SoC 및 편차 */}
      <section className="user__certification station grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">배터리 대여 SoC 및 편차</p>
          </div>
          <div className="list__wrap">
            <ul className="soc">
              <li>
                <dl>
                  <dt>SoC</dt>
                  <dd>
                    <input type="text" className="custom__input" />
                    <span>%이상</span>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>배터리간 편차</dt>
                  <dd>
                    <input type="number" className="custom__input" defaultValue="50" />
                    <span>%이내</span>
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 배터리 대여 SoC 및 편차 */}
      {/* 타임아웃(초) */}
      <section className="user__certification grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">타임아웃(초)</p>
          </div>
          <div className="list__wrap">
            <ul className="time">
              <li>
                <dl>
                  <dt>인증 타임아웃</dt>
                  <dd>
                    <input
                      type="number"
                      className="custom__input"
                      // defaultValue={row.certificationDate}
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>반납 타임아웃</dt>
                  <dd>
                    <input
                      type="number"
                      className="custom__input" // defaultValue={row.platform}
                    />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>대여 타임아웃</dt>
                  <dd>
                    <input
                      type="number"
                      className="custom__input" // defaultValue={row.appVersion}
                    />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 타임아웃(초) */}
      {/* 전송주기 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">전송주기</p>
          </div>
          <div className="list__wrap">
            <ul className="interval">
              <li>
                <dl>
                  <dt>상태전송</dt>
                  <dd>
                    <input type="text" className="custom__input" />
                    <span>분</span>
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>운영정보 수신시간</dt>
                  <dd>
                    <TimePicker
                      value={value1}
                      onChange={setValue1}
                      renderInput={(params) => <TextField {...params} />}
                    />
                    <span>매일 해당시간에 수신</span>
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 전송주기 */}
      {/* 로그파일수집 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">로그파일수집</p>
          </div>
          <div className="list__wrap">
            <ul className="logfile">
              <li>
                <select className="custom__select">
                  <option value="자동수집">자동수집</option>
                </select>
              </li>
              <li>
                <TimePicker
                  value={value1}
                  onChange={setValue1}
                  renderInput={(params) => <TextField {...params} />}
                />
                <span>매일 해당 시간에 전일 생성된 로그파일 수집</span>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 로그파일수집 */}
      {/* 점검화면 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">점검화면</p>
          </div>
          <div className="list__wrap">
            <ul className="screen">
              <li>
                <dl>
                  <dt>화면선택</dt>
                  <dd>
                    <select className="custom__select">
                      <option value="서비스 점검중1">서비스 점검중1</option>
                    </select>
                  </dd>
                </dl>
              </li>
              <li>
                <button className="custom__btn">
                  <span className="f__medium">미리보기</span>
                </button>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 점검화면 */}
    </div>
  );
}

export default AddOrUpdate;
